import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import bgImage2 from "assets/images/aparatura.jpg";
import doctor1 from "assets/images/doctor1.jpg";
import otziv from "assets/images/otzivi.jpg";
import skidki from "assets/images/skidki.jpg";
import news from "assets/images/news.jpg";
import MKBadge from "../../../components/MKBadge";
import MKTypography from "../../../components/MKTypography";
import BackgroundBlogCard from "../../../examples/Cards/BlogCards/BackgroundBlogCard";
import MKBox from "../../../components/MKBox";
import ServicesCard from "../Cards/ServicesCard";

function Services() {
  return (
    <>
      <Grid
        container
        item
        xs={12}
        lg={6}
        flexDirection="column"
        alignItems="center"
        sx={{ textAlign: "center", mt: 10, mb: 2, mx: "auto", px: 0.75 }}
      >
        <MKBadge variant="contained" color="info" badgeContent="" container />
        <MKTypography variant="h1" fontWeight="bold">
            
        </MKTypography>
      </Grid>
      <MKBox component="section" py={2}>
        <Container>
          <Grid mb={3}>
            <ServicesCard
              image={bgImage2}
              title=""
              description="     ."
              action={{
                type: "internal",
                route: "/pages/landing-pages/services",
                label: " ",
              }}
            />
          </Grid>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} lg={3}>
              <BackgroundBlogCard
                image={doctor1}
                title=" "
                description="      "
                action={{
                  type: "internal",
                  route: "/pages/landing-pages/specialist",
                  label: "",
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} lg={3}>
              <BackgroundBlogCard
                image={news}
                title="  "
                description="    ."
                action={{
                  type: "internal",
                  route: "/pages/landing-pages/news",
                  label: "",
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} lg={3}>
              <BackgroundBlogCard
                image={otziv}
                title=" "
                description="   ,    ?"
                action={{
                  type: "internal",
                  route: "/pages/landing-pages/reviews",
                  label: "",
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} lg={3}>
              <BackgroundBlogCard
                image={skidki}
                title=""
                description="   !"
                action={{
                  type: "internal",
                  route: "/pages/landing-pages/sale",
                  label: "",
                }}
              />
            </Grid>
          </Grid>
        </Container>
      </MKBox>
    </>
  );
}
export default Services;
